.app-wrapper {
    display: grid;
    position: relative;
    overflow: hidden;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    gap: 10px;
}
.initializeComponent{
    width: max-content;
    margin: 40vh auto;
    font-size: 30px;
    color: white;
}

.app-wrapper-content {
    grid-area: c;
    background: linear-gradient(-130deg, black, rgb(134, 116, 82));
    color: wheat;
    text-shadow: 0 0 2px black;
}

.active {
    transition: .3s;
    text-shadow: 0 0 15px wheat;
    border: 1px solid rgba(245, 222, 179, 0.27);
    padding: 3px 10px;
    border-radius: 10px;

}

img {
    width: 100%;
}

@media (max-width: 500px) {
    .app-wrapper {
        display: grid;

        grid-template-areas:
    "h h"
    "c c";
        grid-template-rows: 60px 1fr;
        grid-template-columns: 100vw;
        gap: 0;
    }

    .app-wrapper-content {
        min-height: 100vh;
    }

}