.header_img {
  max-width: 50px;
}

.header {
  grid-area: h;
  background: linear-gradient(90deg, rgb(105, 5, 60), rgb(29, 27, 27));
  padding-left: 10px;
  padding-top: 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header button{
  padding: 8px 15px;
  font-size: 18px;
  margin-right: 2vw;
  color: wheat;
 }
.status{
  width: 12px;
  height: 12px;
  background-color: green;
  border-radius: 50%;
}
.login{
  display: flex;
  gap: 1vw;
  align-items: center;
  color: wheat;
  margin-right: 2vw;
}
.loading{
  max-width: 20px;
}
.online{
  text-decoration: none;
  box-shadow: none;
  border: none;
}
.online:hover{
  box-shadow: none;
  border: none;
}
.onClickButton{
  z-index: 3;
  display: flex;
  flex-direction: column;
}
.gap{
  gap: 1vh;
}
.button{
  display: none;
  margin-top: 4px;
  width: 60px;
  height: 3px;
  border-bottom: 2px solid wheat;
  opacity: 90%;
}

.swimmingMenu{
  position: absolute;
  z-index: 2;
  height: 100vh;
  width: 100vw;
  background-color: black;
  opacity: 90%;
  top: -100vh;
  left: 100vw;
  transition: .6s;
}
.swimmingMenuOpen{
   top: 0;
   left: 0;
   transition: .6s;
 }

@media (max-width: 500px){
  .header{
    font-size: 12px;
  }
  .headerImg{
    display: none;
  }
  .button{
    display: block;
  }
}