.container{
    margin: 20px 0 20px 0;
    max-width: 90vw;
}

.status{
    padding: 5px 10px;
    border: 1px solid wheat;
    border-radius: 10px;
    cursor: pointer;
    opacity: 80%;
    max-width: 80vw;;

}

.nullStatus{
    cursor: pointer;
    padding: 5px 10px;
    border: 1px solid wheat;
    border-radius: 10px;
    opacity: 50%;
}

