.container {
    margin: 5vh 5vw;
    display: flex;
    gap: 2vw;
    align-items: center;
}

.image {
    max-width: 200px;

}

.image img {
    border-radius: 50%;
}

.userData{
    width: 20vw;
    position: relative;
}

.input {
    background: transparent;
    width: 50%;
    margin-left: 1vw;
    color: inherit;
}

.status {
    width: 100%;
    color: inherit;
    background: transparent;
    padding: 10px;
    border: 1px solid rgba(245, 222, 179, 0.35);
    margin: 10px auto;
    border-radius: 10px;
}

.header button {
    margin: 0;
    color: inherit;
}

.pages {
    margin: 3vh 0 0 3vw;
    border-bottom: 1px solid wheat;
    max-width: max-content;
    padding-bottom: 1vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.pageInput {
    text-shadow: 0 0 10px wheat;
    font-weight: bold;
    text-align: center;
    max-width: 5vw;
    border: none;
    background: transparent;
    font-size: 20px;
    color: inherit;
    margin-bottom: 0;
}

.pagesNumber {
    margin-right: 2vw;
    font-size: 30px;
}

.totalPages {
    margin-left: 10vw;
    font-size: 30px;
    border: 1px solid wheat;
    padding: 10px;
    border-radius: 10px;
}

.totalPagesNumber {
    text-shadow: 0 0 10px wheat;
}
.loading{
    max-width: 150px;
    position: absolute;
    left: 40%;
}
.flag{
    width: 10px;
    height: 10px;
    background-color: transparent;
    margin-left: 1vw;
    border-radius: 50%;
    text-align: center;
}
.createChat{
    margin-left: 1vw;
    text-decoration: none;
}
.createChat button{
    font-size: 18px;
}

.inputOfPageNumber{
    display: flex;
    align-items: flex-end;
}

@media (max-width: 500px) {
    .pagesNumber {
        font-size: 20px;
    }
    .pageInput{
        max-width: 15vw;
    }

    .inputOfPageNumber{
        margin-right: 7vw;
    }
    .pages button{
        font-size: 18px;
    }
    .pages{
        flex-wrap: wrap;
    }
    .totalPages{
       margin-left: 0;
        font-size: 18px;
    }
    .container img{
        max-width: 80%;
    }
    .userData{
        font-size: 12px;
        width: 30vw;
    }
    .userData h2{
        margin: 5px;
    }
    .userData input{
        margin: 5px;
    }
    .userData button{
        margin: 5px;
        font-size: 10px;
    }
    .userData button:first-child{
        width: 10vw;
    }
    .status{
        padding: 3px 7px;
        width: 90%;
        font-size: 12px;
    }
    .loading{
       max-width: 100px;
       left: 65%;
        margin-top: 6vh;
    }
}
