.header{
    width: 20%;
    height: 40%;
    background: rgba(245, 245, 245, 0.58);
    margin: 10vh auto;
    border: 1px solid blanchedalmond;
    border-radius: 10px;
    padding-top: 10vh;
}
.header input{
    background-color: transparent;
    width: 90%;
    margin-top: 10px;
    margin-left: 2px;
    border-radius: 10px;
    height: 3vh;
}
.header input:last-child{
    width: 10%;
    margin-left: 1vw;
    border-radius: 5px;
    margin-bottom: 3.2vh;
}
.link{
    color: inherit;
}
.buttons{
    max-width: max-content;
    margin: 20px 20px;
}
a{
    text-decoration: none;
    color: wheat;
}
.formHeader button{
    display: inline-block;
    margin: 10px;
}

.checkBox{
    display: flex;
    align-items: flex-end;
}

@media (max-width: 1500px) {
    .header{
        width: 70%;
        height: 50%;
    }
}
@media (max-width: 500px){
    .checkBox h2{
        font-size: 14px;
    }
    .header input:last-child{
        margin-bottom: 1.2vh;
    }
}