.nav {
  grid-area: n;
  background-color: rgb(29, 27, 27);
  display: flex;
  flex-direction: column;
  gap: 5vh;
  align-items: center;
  height: 100vh;
  border: 1px solid wheat;

}
.item {
  text-shadow: 0 0 2px black;
  font-size: 24px;
}
.item:first-child {
  margin-top: 5vh;
}
.item a {
  color: wheat;
  text-decoration: none;
}
.item:last-child {
  margin-top: 5vh;
}



@media (max-width: 500px) {
  .nav{
    display: none;
  }
}