
.banner img {
  width: 100%;
}
.avatar {
  max-width: 200px;
  border-radius: 50%;
  border: 3px solid wheat;
  cursor: pointer;
}
.mySelf {
  margin-top: 30px;
  display: flex;
  gap: 4vw;
  align-items: center;
}
.userInfo {
  padding-top: 20px;
  position: relative;
  font-family:"Arial Narrow";
  max-width: 400px;
  color: azure;
}
button{
  color: inherit;
}
.input{
  text-decoration: none;

}
.placeholder{
  opacity: 50%;
}
.aboutMe{

  margin: 0 0 20px;
  padding: 5px 10px;
  border: 1px solid wheat;
  border-radius: 10px;
  color: wheat;
}
.userInfo h1{
  color: wheat;
  text-shadow: 0 0 5px black;
}
.editModeInfo{
  font-family:"Arial Narrow";
}

.editModeInfo h3{
  font-size: 14px;
  margin: 0;
}
.editModeInfo, input, textarea{
  font-size: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  width: 20vw;
  opacity: 80%;
}
.button{
  color: wheat;
  position: absolute;
  top: 0;
  right: 0;
}
.navLink{
  text-decoration: none;
  border: none;
}

@media (max-width: 850px){
  .mySelf{
    flex-direction: column;
  }
  .editModeInfo, input, textarea{
    width: 90vw;
  }
}
@media (max-width: 450px){
  .mySelf{
    font-size: 70%;
  }
  .userInfo{
    max-width: 340px;
  }

}