
section {
    display: grid;
    grid-template-columns:30% 70%;
    grid-auto-rows: 5% 83%;
    grid-template-areas:
    "n h"
    "n c";
    min-height: 100vh;
    height: max-content;
}

.contacts {
    grid-area: n;
    overflow-y: scroll;
    max-height: 100vh;
}

.messageHeader {
    grid-area: h;
}

.messages {
    grid-area: c;
}

.contacts, .messages {
    border-right: 1px solid rgba(0, 0, 0, 0.21);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5vh;
}


.contact:first-child, .message:first-child {
    margin-top: 10vh;
}

.message {
    display: flex;
    align-items: center;
}

.contact {
    display: flex;
    align-items: center;
    animation: preview 1.5s linear;
}

@keyframes preview {
    0% {
        opacity: 0;

    }

    100% {
        opacity: 100%;

    }
}

.contact, .message {
    margin-left: 5vw;
    font-size: 24px;
    height: 80px;
}

.message div {
    margin-bottom: 3px;
}

.contact a {
    color: wheat;
    text-decoration: none;
    border-bottom: 1px solid rgba(245, 222, 179, 0.33);
}

.contact img {
    max-width: 20%;
    margin-right: 1vw;
    border-radius: 50%;
}

.sendButton {
    float: right;
    margin: 20px 20px 0 0;
}

.textArea {
    width: 95%;
    height: 100px;
    position: relative;
    background-color: transparent;
    color: wheat;
}

.messages {
    border-right: none;
    margin-top: 2vh;
    width: 80%;
    display: grid;
    max-height: 80vh;
    overflow-y: scroll;

}

.messages::-webkit-scrollbar, .contacts::-webkit-scrollbar{
    width: 0;
}

.friend {
    order: -1;
    color: #989f2f;
    margin-left: 1vw;
    text-shadow: 0 0 10px #989f2f;
    box-shadow: 0 0 10px #989f2f;
    margin-right: 10px;
    padding: 5px;
    border: 1px solid #989f2f;
    border-radius: 10px;
}

.user {
    order: 0;
    margin-left: 10px;
    color: blanchedalmond;
    text-shadow: 0 0 10px blanchedalmond;
    box-shadow: 0 0 10px blanchedalmond;
    margin-right: 1vw;
    padding: 5px;
    border: 1px solid blanchedalmond;
    border-radius: 10px;
}

.userMessage {
    max-width: 20vw;
    order: 0;
    padding: 3px;
    color: blanchedalmond;
    border-bottom: 1px solid wheat;
}

.friendMessage {
    max-width: 20vw;
    color: #989f2f;
    margin-left: auto;
    order: -2;
    padding: 3px;
    border-bottom: 1px solid #989f2f;
}

.spans {
    display: flex;
    align-items: center;

}

.spanUser {
    display: flex;
    align-items: center;
    margin-left: 5px;

}

.viewed {
    width: 10px;
    height: 10px;
    background-color: green;
    border-radius: 50%;
}

.contacts h2, .contactMini h2 {
    margin-left: 1vw;
    padding: 5px;
    min-width: 20px;
    min-height: 20px;
    text-align: center;
    border-radius: 50%;
    font-size: 18px;
    color: white;
    background-color: blue;
    animation: 1s puls linear infinite;
}

@keyframes puls {
    0% {
        transform: scale(1.0, 1.0);
    }
    50% {
        transform: scale(1.3, 1.3);
    }
    100% {
        transform: scale(1.0, 1.0);
    }
}

.time {
    opacity: 50%;
    font-size: 10px;
    order: -1;
}

.time h2 {
    font-size: inherit;
}

.selectMessageForDelete {
    width: 20%;
    margin-top: 1vh;
}

.viewMore {
    background: transparent;
    font-size: 18px;
    padding: 0;
    width: max-content;
    margin: 0 auto;
}

.viewMore:hover {
    box-shadow: none;
}

.selectMessage {
    text-shadow: none;
    font-size: 18px;
    background: transparent;
    color: red;
}

.selectMessageGreen {
    font-size: 18px;
    background: transparent;
    color: green;
}

.selectMessage:hover {
    transform: none;
    text-shadow: 0 0 5px red;
    box-shadow: none;
    color: red;
}

.selectMessageGreen:hover {
    transform: none;
    text-shadow: 0 0 5px green;
    box-shadow: none;
    color: green;
}

.totalCount {
    margin-left: 1vw;
}

.messageHeader {
    position: relative;
    padding-bottom: 5px;
    border-bottom: 1px solid wheat;
}

.backToUsers {
    display: none;
    position: absolute;
    left: 1vw;
}

.smallUsersMenu {
    z-index: 2;
    position: absolute;
    border: 1px solid wheat;
    border-radius: 5px;
    min-height: max-content;
    width: 60vw;
    left: -61vw;
    top: 8%;
    background-color: rgba(0, 0, 0, 0.80);
    transition: 0.6s;
}

.open {
    left: 0;
    transition: 0.6s;
}

.closeUsers {
    position: absolute;
    top: 1%;
    right: 1%;
}

.contactsMini {
    margin-top: 8vh;
    margin-bottom: 8vh;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    gap: 3vh;
    height: 100vh;
    overflow-y: scroll;
}

.contactMini a {
    color: wheat;

}

.contactMini {
    display: flex;
    align-items: center;
    margin-left: 1vw;
    gap: 2vw;
}

.contactsMini img {
    width: 25%;
    border-radius: 50%;
}

@media (max-width: 850px) {
    .friendMessage, .userMessage{
        max-width: 65vw;
    }
    .contacts {
        display: none;
    }

    section {
        grid-template-areas:
    "h h"
    "c c";
    }

    .messages {
        font-size: 12px;
        width: 99%;
    }

    .time {
        display: none;
    }

    .messageHeader {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 1vw;
        padding-right: 1vw;
    }

    .selectMessageForDelete {
        width: 30%;
        margin-top: 0;
    }
    .backToUsers{
        display: block;
    }
}